import React, { lazy, Suspense } from 'react'
import { Redirect, Route, Switch } from 'react-router-dom';
import Loading from 'components/shared-components/Loading';

const Dashboard = lazy(() => import('./dashboard'));
const Report = lazy(() => import('./report'));
const Attendance = lazy(() => import('./attendance'));
const MarkConsolidation = lazy(() => import('./mark-consolidation'));
const ConsolidatedReport = lazy(() => import('./consolidated-report'));

const Index = (props) => {

    const { match } = props

    const routes = [
        {
            name: 'Хянах самбар',
            key: 'view_dashboard',
            route: 'dashboard',
            component: Dashboard
        },
        {
            name: 'Нэгдсэн тайлан',
            key: 'view_report',
            route: 'consolidated-report',
            component: ConsolidatedReport
        },
        {
            name: 'Ирцийн тайлан',
            key: 'view_attendance',
            route: 'attendance',
            component: Attendance
        },
        {
            name: 'Дүнгийн тайлан',
            key: 'view_report',
            route: 'mark',
            component: Report
        },
        {
            name: 'Дүнгийн нэгтгэл',
            key: 'view_report',
            route: 'mark-consolidation',
            component: MarkConsolidation
        },
        {
            name: 'Шалгалтын тайлан',
            key: 'view_report',
            route: 'online-test-report',
            component: Report
        },
        {
            name: 'Шалгалтын асуултын тайлан',
            key: 'view_report',
            route: 'online-test-question-report',
            component: Report
        }
    ]

    return (
        <Suspense fallback={<Loading />}>
            <Switch>
                { routes.map((route, index) => (
                    <Route
                        key={index}
                        path={`${match.url}/${route.route}`} 
                        component={route.component} 
                    />
                ))}
                <Redirect from={`${match.url}`} to={`${match.url}/dashboard`} />
            </Switch>
        </Suspense>
    )
}

export default Index